@import url(https://fonts.googleapis.com/css2?family=Frijole&display=swap);
@font-face{font-family:"Anor";src:url(/static/media/Archi10.083787dc.TTF) format("truetype")}@font-face{font-family:'Abw';src:url(/static/media/Archi18.329828b4.TTF) format("truetype")}:root{--grey: #676767;font-size:calc( 6px + 1vw)}*{box-sizing:border-box}body{margin:0;min-height:100vh;width:100%;border:1px solid black}a{text-decoration:none;color:inherit}p{margin:0}

.landing-page__title{align-self:center;margin-bottom:2rem}.landing-page__title h1{font-weight:200;font-size:4em;margin:0;letter-spacing:.2em;cursor:pointer}.landing-page__title h2{font-size:1.3em;margin:0;letter-spacing:.18em}

.App{font-family:Georgia, 'Times New Roman', Times, serif;color:var(--grey);text-align:center;display:grid;grid-template-columns:1fr;grid-template-rows:repeat(4, auto);min-height:99vh}.App #out{font-size:3.5em;font-weight:900}.App #enter-btn,.App #order-here{font-size:2.8rem;letter-spacing:.1em;font-weight:900;height:2em;width:7em;position:relative;align-self:end;margin:0 auto;font-family:Georgia, 'Times New Roman', Times, serif;background-color:#00000000;border:none;color:inherit;outline:none}.App #enter-btn:hover,.App #order-here:hover{cursor:pointer}.App #order-here{display:block;position:relative;margin:1em auto 2em;width:10em;font-size:2rem;border:1px solid var(--grey);opacity:.8}.about-page__container{font-family:Georgia, 'Times New Roman', Times, serif;color:var(--grey);text-align:center;display:grid;grid-template-columns:1fr;grid-template-rows:repeat(4, 1fr);min-height:100vh;min-width:100vw}@media (min-width: 900px){.App h3{font-size:2.5rem}.App #enter-btn{font-size:2rem}}

.footer{width:100%;overflow:hidden;align-self:flex-end;background-color:#67678010;padding:1em 0;color:var(--grey);font-size:1rem}.footer p{padding:0.2em 0}.footer .footer-logo__section{display:flex;justify-content:center;align-items:center}.footer .footer-logo__section a img{width:3em;margin:0 1em}.footer .footer-contact-images{display:flex;justify-content:center;align-items:center;padding-top:.5em}.footer .footer-contact-images a img{width:1.5em;margin:0 .7em}.footer .footer-credits #wd{font-family:'Frijole', cursive;font-size:.9rem}@media (min-width: 900px){.footer{font-size:.7rem}.footer .footer-credits #wd{font-size:.5rem}}

.background{position:fixed;z-index:-100;height:100%;width:100%}.background img{height:100%;width:100%;object-fit:cover}

.about-container{display:flex;flex-direction:column;max-width:1200px;position:relative;margin:0 auto}.about-container .about-container__snippets{padding:2em 1em}.about-container .about-container__snippets p{letter-spacing:.2em;font-size:1.4rem;padding:1em 0}.about-container .about-container__snippets iframe{opacity:.7;z-index:10}@media (min-width: 900px){.about-container{flex-direction:row;flex-wrap:wrap}.about-container .about-telurian{order:2;width:60%}.about-container .about-telurian p{text-indent:0;text-align:left;font-size:1.4rem}.about-container .about-telurian h3{text-align:left;font-size:2rem}.about-container .about-container__snippets{order:3;width:100%}.about-container .opuses-container{order:1;width:40%;text-align:right}.about-container .opuses-container .opus{font-size:1.4rem}.about-container .opuses-container .opus p{font-size:2rem}}

.opus{font-size:1.6rem;list-style:none;margin:0;padding:0}.opus p{font-feature-settings:"smcp";font-variant:small-caps;font-size:2.2rem;letter-spacing:.1em;padding:1em 0;font-weight:900}.opus li{padding:.2em 0}

.about-telurian{padding:0 2em}.about-telurian h3{font-size:2.2rem;text-align:center;font-feature-settings:"smcp";font-variant:small-caps}.about-telurian p{text-align:justify;font-size:1.7rem;text-indent:1em}.about-telurian #telurian,.about-telurian #tellurium{font-size:1rem;width:auto;position:relative;opacity:.2;color:#fdd;background-color:var(--grey);border-radius:20px;padding:0 1em}

.items-container{position:relative;max-width:1200px;margin:0 auto}.items-container p:first-child,.items-container #order-page__punch-line{font-feature-settings:"smcp";font-variant:small-caps;font-weight:900;font-size:1.4rem;letter-spacing:.2em;padding:2em 0}.items-container #order-page__punch-line{padding:0 0 1em}.items-container .order-page__shipping{text-align:left;padding:0 2em;font-size:1.2rem}.items-container .order-page__shipping p:nth-child(2),.items-container .order-page__shipping p:nth-child(5),.items-container .order-page__shipping p:nth-child(8),.items-container .order-page__shipping p:nth-child(11),.items-container .order-page__shipping p:nth-child(12){font-weight:900;margin:1em 0}

.item{display:flex;flex-wrap:wrap;padding:3em 2em;color:var(--grey)}.item .item-photo{align-self:start;display:flex;flex-direction:column;width:35%}.item .item-photo img{width:100%}.item .item-photo button{margin:-1.3em 0;font-size:1rem;width:auto;color:var(--grey);border:none;background-color:#00000000;cursor:pointer;text-transform:uppercase;font-weight:900;text-shadow:1.5px 1.5px 1px white}.item .item-photo .paypal{position:relative;margin:0 auto;width:100%}.item .item-photo .paypal select{width:100%;font-size:.4rem}.item .item-photo .paypal input{width:100%}.item .item-photo .paypal .buy-now{border:1px solid var(--grey);border-radius:5px;padding:0 1em;width:97%;opacity:.8}.item .item-description{align-self:start;padding-left:2em;width:65%}.item .item-description h3{margin:0;padding-bottom:1em;font-size:1.4rem;text-align:left}.item .item-description p{text-align:left;font-size:1.3rem}.item .item-description p:last-child{padding-top:1em;font-weight:900}@media (min-width: 900px){.item .item-photo .paypal{width:100%}.item .item-photo .paypal select{width:100%;font-size:1rem;color:var(--grey)}.item .item-photo .paypal .buy-now{margin-top:1em;width:60%}.item .item-description h3{font-size:1.2rem}.item .item-description p{font-size:1.1rem}}

